import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Home from "./Home";

ReactDOM.render(
	<Router>
		<Switch>
			<Route path="/">
				<Home />
			</Route>
		</Switch>
	</Router>,
	document.getElementById("root")
);
